<template>
  <div class="wrapper">
    <img class="wrapper__img" src="http://www.dell-lee.com/imgs/vue3/user.png"/>
    <div class="wrapper__input">
      <input
       class="wrapper__input__content"
       placeholder="请输入用户名"
       v-model="username"
    />
    </div>
    <div class="wrapper__input">
      <input
        type="password"
        class="wrapper__input__content"
        placeholder="请输入密码"
        autocomplete="new-password"
        v-model="password"
      />
    </div>
    <div class="wrapper__input">
      <input
        type="password"
        class="wrapper__input__content"
        placeholder="确认密码"
        autocomplete="new-password"
        v-model="ensurement"
      />
    </div>
    <div class="wrapper__register-button" @click="handleReginsterClick">注册</div>
    <div class="wrapper__register-link" @click="handleLoginView">已有账号去登陆</div>
  </div>
  <Toast v-if="show" :message="toastMessage"/>
</template>

<script>
import { useRouter } from 'vue-router'
import { reactive, toRefs } from 'vue'
import { get } from '../../utils/request'
import Toast, { useToastEffect } from '../../components/Toast.vue'

const useReginsterEffect = (showToast) => {
  const router = useRouter()
  const data = reactive({
    username: '',
    password: '',
    ensurement: ''
  })

  const handleReginsterClick = async () => {
    try {
      const result = await get('/login')
      if (result[0].password === '') {
        router.push({ name: 'LoginView' })
      } else {
        showToast('注册失败')
      }
    } catch (e) {
      showToast('请求失败')
    }
  }
  const { username, password, ensurement } = toRefs(data)
  return { username, password, ensurement, handleReginsterClick }
}

const useLoginEffect = () => {
  const router = useRouter()
  const handleLoginView = () => {
    router.push({ name: 'LoginView' })
  }
  return { handleLoginView }
}
/* eslint-disable */
export default {
  name: 'RegisterView',
  components: {
    Toast
  },
  setup() {
    const { show, toastMessage, showToast } = useToastEffect()
    const { username, password, ensurement, handleReginsterClick } = useReginsterEffect(showToast)
    const { handleLoginView } = useLoginEffect()

    return {
      show, toastMessage,
      username, password, ensurement,
      handleReginsterClick, handleLoginView
    }
  }
}
/* eslint-enable */
</script>

<style lang="scss" scoped>
@import '../../style/virables.scss';
.wrapper {
  position: absolute;
  top: 50%;
  left: 0;
  right: 0;
  transform: translateY(-50%);
  &__img {
    display: block;
    margin: 0 auto .4rem auto;
    width: .66rem;
    height: .66rem;
  }
  &__input {
    height: .48rem;
    margin: 0 .4rem .16rem .4rem;
    padding: 0 .16rem;
    background: #F9F9F9;
    border: .01rem solid rgba(0,0,0,0.10);
    border-radius: .06rem;
    border-radius: .06rem;
    &__content {
      line-height: .48rem;
      border: none;
      outline: none;
      width: 100%;
      background: none;
      font-size: .16rem;
      color: $content-notice-fontColor;
      &::placeholder {
        color: $content-notice-fontColor;
      }
    }
  }
  &__register-button {
    margin: .32rem .4rem .16rem .4rem;
    line-height: .48rem;
    background: #0091FF;
    box-shadow: 0 .04rem .08rem 0 rgba(0,145,255,0.32);
    border-radius: .04rem;
    border-radius: .04rem;
    color: #fff;
    font-size: .16rem;
    text-align: center;
  }
  &__register-link {
    text-align: center;
    font-size: .14rem;
    color: $content-notice-fontColor;
    text-decoration: none;
    cursor:pointer;
    &:hover{
      text-decoration: underline;
    }
  }
}
</style>
